.root
    max-width: 700px
    overflow: hidden
.container
    width: 100%
    margin-bottom: 20px
    display: flex
    flex-direction: row
    align-items: center
    align-content: center
    justify-content: center

.return
    padding: 5px 15px
    background-color: #7AC751
    border: none
    outline: none
    color: white
    cursor: pointer
    margin-bottom: 10px
    border-radius: 10px
    &:hover
        box-shadow: 0px 0px 30px #4FD81F

.left
    border-bottom: 1px solid #EFEFEF

.image
    max-width: 250px
    max-height: 250px
    cursor: pointer

.right
    height: 100%
    padding-left: 15px
    border-bottom: 1px solid #EFEFEF
    display: flex
    flex-direction: column
    justify-content: space-between

.title
    margin-bottom: 10px
    font-size: 28px
    font-weight: 600
    line-height: 34px
    cursor: pointer

.description
    margin-bottom: 90px
    max-height: 90px
    max-width: 450px
    font-size: 16px
    font-weight: 400
    line-height: 24px
    
.priceBox
    display: flex
    flex-direction: row
    align-items: center

.priceAmount
    margin-right: 15px
    font-size: 24px
    font-weight: 500
    line-height: 32px

.delete
    background-color: red
    color: white
    border-radius: 3px
    outline: none
    border: none
    &:hover
        box-shadow: 0px 0px 30px #4FD81F
    &:active
        background-color: #843838

@media screen and (max-width: 550px) 
    .image
        max-width: 150px
        max-height: 150px  

    .title
        margin-bottom: 5px
        font-size: 16px
        line-height: 18px
       
    .description
        margin-bottom: 10px
        max-width: 450px
        font-size: 12px
        font-weight: 400
        line-height: 14px
        
    .priceAmount
        padding-left: 20px
        margin-right: 30px
        font-size: 16px
        line-height: 18px